.agitation {
  padding-top: 54px;
}
.agitation__text {
  color: #283645;
  text-align: center;
  line-height: 30px;
  margin: 0;
}
.comment__user-name {
  color: #283645;
  font-weight: bold;
}
.comment__user-message {
  color: #283645;
  line-height: 24px;
}
.comment__time {
  text-transform: uppercase;
  font-size: 12px;
  line-height: 24px;
  color: #283645;
  font-weight: 300;
}
.download {
  color: #ffffff;
  background-color: #1d2631;
}
.download__button {
  font-size: 18px;
  line-height: 18px;
}
.download__availability {
  line-height: 24px;
}
.logo-wrapper {
  background-color: #1d2631;
}
.main-nav {
  background-color: #283645;
}
.offer__opportunity {
  background-color: #f2f2f2;
}
.offer__question {
  font-size: 24px;
  color: #283645;
}
.offer__slogan {
  font-size: 24px;
  color: #283645;
}
.opportunity__item-head {
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  text-align: center;
  color: #283645;
}
.opportunity__item-body {
  text-align: center;
  color: #283645;
}
body {
  font-size: 14px;
  font-family: 'Open Sans', Arial, sans-serif;
  line-height: 24px;
}
a {
  color: #fff;
  text-decoration: none;
}
.page-footer__develop-link {
  color: #283645;
  line-height: 18px;
}
.page-header__title {
  color: #ffffff;
  font-size: 24px;
  font-weight: lighter;
  text-align: center;
  margin-top: 0;
  padding-top: 35px;
}
.page-header__photo {
  margin: 0;
}
.photo__wrapper-image {
  background-color: #2b3b4b;
  background-image: url(/source/img/bg-back-mobile@1x.jpg);
  background-repeat: no-repeat;
  width: 320px;
  height: 240px;
}
.site-list {
  padding: 0;
  margin: 0;
}
.site-list__item {
  text-transform: uppercase;
  font-size: 18px;
  list-style: none;
  text-align: center;
}
.user-photo__comment {
  background-color: #f2f2f2;
}
.user-photo__name {
  text-transform: uppercase;
  background-color: #d22856;
  color: #ffffff;
}
